}
.export-part-template-module-render__configWrap--ADzg4 {
    display: flex;
    flex-direction: column;
    margin-top: 2px;
}
.export-part-template-module-render__configWrap--ADzg4 .export-part-template-module-render__main--HQTpU {
    display: flex;
    align-items: center;
}
.export-part-template-module-render__configWrap--ADzg4 .export-part-template-module-render__desc--KubFW {
    color: #999;
    margin-top: -5px;
}
`, "",{"version":3,"sources":["webpack://./src/components/export-part-template-module-render.vue"],"names":[],"mappings":";AAmgCE;IACE;+BAC2B;IAC3B,kBAAkB;AACpB;AACA;IACE;2CACuC;AACzC;EACA;;GAEC;AACD;;IAEE,aAAa;AACf;AACA;IACE,iBAAiB;AACnB;AACA;IACE,oBAAoB;IACpB,8BAA8B;IAC9B,WAAW;IACX,mBAAmB;AACrB;AAEA;IACE,oBAAoB;AACtB;AACA;;IAEE,YAAY;AACd;AACA;IACE,yBAAyB;IACzB,qDAAqD;IACrD,kBAAkB;IAClB,2CAA2C;IAC3C,yDAAyD;AAC3D;EACA,mCAAmC;AACnC;;;IAGE,qBAAqB;IACrB,eAAe;AACjB;AACA;IACE,aAAa;IACb,8BAA8B;IAC9B,kBAAkB;AACpB;AACA;IACE,yBAAyB;IACzB,gBAAgB;AAClB;AACA;IACE,yBAAyB;IACzB,iBAAiB;AACnB;AACA;IACE,iBAAiB;AACnB;AACA;IACE,eAAe;IACf,gBAAgB;IAChB,mBAAmB;AACrB;AACA;IACE,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,qBAAqB;IACrB,eAAe;IACf,yCAAyC;IACzC,cAAc;AAChB;AACA;IACE;;KAEC;AACH;AACA;IACE,YAAY;IACZ,eAAe;AACjB;AACA;IACE,yBAAyB;IACzB;;KAEC;IACD,kBAAkB;IAClB;;KAEC;IACD,kBAAkB;IAClB,iBAAiB;AACnB;AACA;IACE,gBAAgB;AAClB;AACA;IACE,eAAe;IACf,iBAAiB;IACjB,oBAAoB;AACtB;AACA;;IAEE,UAAU;AACZ;AACA;IACE,YAAY;AACd;AACA;IACE,uBAAuB;IACvB;sBACkB;IAClB,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,qBAAqB;IACrB,eAAe;IACf,yCAAyC;IACzC,cAAc;AAChB;AACA;IACE,oBAAoB;IACpB;;KAEC;AACH;AACA;IACE,UAAU;IACV,QAAQ;IACR,YAAY;IACZ,gBAAgB;IAChB,uBAAuB;AACzB;AACA;;IAEE;;KAEC;AACH;AACA;IACE,aAAa;IACb,SAAS;AACX;AACA;IACE,kBAAkB;IAClB,MAAM;IACN,WAAW;IACX,mBAAmB;AACrB;AACA;IACE,kBAAkB;IAClB,UAAU;IACV,QAAQ;IACR,mBAAmB;IACnB,iBAAiB;AACnB;AACA;IACE,gBAAgB;AAClB;AACA;IACE,qBAAqB;AACvB;AACA;IACE,cAAc;AAChB;AACA;IACE,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACpB;AACA;IACE,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,uBAAuB;AACzB;AACA;IACE,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,cAAc;AAChB;AACA;IACE,mBAAmB;AACrB;AACA;;IAEE,aAAa;AACf;AACA;;IAEE,qBAAqB;AACvB;AACA;IACE,WAAW;AACb;AACA;IACE;;KAEC;IACD,aAAa;AACf;AACA;IACE,sCAAsC;IACtC,oBAAoB;IACpB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,sBAAsB;AACxB;AAEA;IACE,eAAe;AACjB;AACA;IACE,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,WAAW;AACb;AACA;IACE,gCAAgC;AAClC;AACA;IACE,mBAAmB;IACnB,gBAAgB;IAChB,gCAAgC;AAClC;AACA;IACE,gBAAgB;AAClB;AACA;IACE,iBAAiB;IACjB,oBAAoB;AACtB;AACA;IACE,aAAa;IACb,sBAAsB;IACtB,eAAe;AACjB;AACA;IACE,aAAa;IACb,mBAAmB;AACrB;AACA;IACE,WAAW;IACX,gBAAgB;AAClB","sourcesContent":["<template>\n  <el-dialog\n    v-model=\"visible\"\n    class=\"s-export-part-template-modal\"\n    width=\"800px\"\n    align-center\n    destroy-on-close\n    append-to-body\n    :close-on-click-modal=\"false\"\n    @close=\"close()\">\n    <template #header>\n      <span style=\"margin-right: 8px\">\n        发布为局部模板\n      </span>\n      <el-tooltip placement=\"top\">\n        <s-others-icon\n          :class=\"$style.titleTipIcon\"\n          name=\"help_line\"></s-others-icon>\n        <template #content>\n          <div style=\"width: 300px\">\n            应用中的部分内容（页面、逻辑、实体等）可导出为局部模板。局部模板可在其他应用中导入，用于快捷创建相同的页面、逻辑、实体等。\n          </div>\n        </template>\n      </el-tooltip>\n    </template>\n    <el-container>\n      <el-aside\n        :class=\"$style.left\"\n        style=\"\n          width: 400px;\n          height: 478px;\n          overflow: scroll;\n        \">\n        <el-form\n          ref=\"formRef\"\n          label-position=\"top\"\n          :model=\"model\"\n          require-asterisk-position=\"right\"\n          @validate=\"formValid = $event.valid\">\n          <div :class=\"$style.symbolbox\">\n            <s-upload-icon\n              v-if=\"!isEditOldTemp\"\n              @changeIcon=\"changeIcon\"\n              :can-edit=\"canEditIcon\"\n              :prop-icon=\"\n                model.icon\n              \"></s-upload-icon>\n            <el-form-item\n              style=\"position: relative\"\n              required\n              :rules=\"\n                isEditOldTemp\n                  ? {}\n                  : validateRules.symbol\n              \"\n              prop=\"symbol\"\n              for=\"none\">\n              <template #label>\n                <span>\n                  {{\n                    isEditOldTemp\n                      ? \"模板名称（标识）\"\n                      : \"模板标识\"\n                  }}\n                </span>\n                <div\n                  :class=\"$style.histroy\"\n                  v-if=\"\n                    historyList.length > 0 &&\n                    isEditOldTemp\n                  \">\n                  <el-link\n                    @click=\"canEditToNewTemp\">\n                    发布为新模板\n                  </el-link>\n                </div>\n\n                <div\n                  :class=\"$style.histroy\"\n                  style=\"left: 203px\"\n                  v-else-if=\"\n                    historyList.length > 0 &&\n                    !isEditOldTemp\n                  \">\n                  <el-popover\n                    width=\"auto\"\n                    trigger=\"click\">\n                    <template #reference>\n                      <el-link>发布历史</el-link>\n                    </template>\n                    <div :class=\"$style.menu\">\n                      <div\n                        :class=\"$style.menuItem\"\n                        v-for=\"(\n                          history, index\n                        ) in historyList\"\n                        :key=\"index\">\n                        <div\n                          style=\"\n                            display: flex;\n                            justify-content: space-between;\n                          \">\n                          <span\n                            :class=\"\n                              $style.menuName\n                            \">\n                            {{ history.name }}\n                          </span>\n                          <el-link\n                            @click=\"\n                              toggleHistory(\n                                history\n                              )\n                            \">\n                            发布到该模板\n                          </el-link>\n                        </div>\n                        <div\n                          :class=\"\n                            $style.menuSymbol\n                          \">\n                          标识：{{\n                            history.symbol\n                          }}\n                        </div>\n                      </div>\n                    </div>\n                  </el-popover>\n                </div>\n                <el-tooltip\n                  v-if=\"!isEditOldTemp\"\n                  type=\"help\"\n                  placement=\"top\"\n                  :class=\"$style.histroy\"\n                  style=\"\n                    left: 64px;\n                    margin-top: 4px;\n                  \"\n                  size=\"auto\">\n                  <s-others-icon\n                    name=\"help_line\"\n                    style=\"\n                      margin-left: 8px;\n                    \"></s-others-icon>\n                  <template #content>\n                    <div style=\"width: 300px\">\n                      若发布为已存在的局部模板的“新版本”，则请输入已存在的局部模板标识；若发布为新的局部模板发布，则请输入新的唯一模板标识。\n                    </div>\n                  </template>\n                </el-tooltip>\n              </template>\n              <el-input\n                v-if=\"!isEditOldTemp\"\n                v-model=\"model.symbol\"\n                maxlength=\"100\"\n                :disabled=\"loading\"\n                maxlength-message=\"标识最长100字符\"\n                placeholder=\"请输入\"\n                style=\"width: 252px\"></el-input>\n              <el-select\n                v-else\n                style=\"width: 336px\"\n                placeholder=\"请选择模板历史\"\n                v-model=\"lastItem.symbol\"\n                :data=\"historyEditList\"\n                @change=\"\n                  onSelectSegmentTemp($event)\n                \">\n                <el-option\n                  v-for=\"option in historyEditList\"\n                  :key=\"option.value\"\n                  :label=\"option.label\"\n                  :value=\"\n                    option.value\n                  \"></el-option>\n              </el-select>\n            </el-form-item>\n          </div>\n          <el-form-item\n            v-if=\"!isEditOldTemp\"\n            label=\"模板名称\"\n            required\n            :rules=\"validateRules.name\"\n            prop=\"name\"\n            for=\"none\">\n            <el-input\n              v-model=\"model.name\"\n              :disabled=\"loading\"\n              maxlength=\"50\"\n              maxlength-message=\"模板名称最长50字符\"\n              placeholder=\"请输入\"></el-input>\n          </el-form-item>\n          <el-form-item\n            v-if=\"!isEditOldTemp\"\n            style=\"position: relative\"\n            label=\"模板分类（资产中心展示）\"\n            required\n            :rules=\"validateRules.tags\"\n            prop=\"tags\"\n            for=\"none\">\n            <div :class=\"$style.tagcategory\">\n              <el-link\n                :disabled=\"!checkPermission\"\n                @click=\"openAssetconfig\">\n                分类管理\n                <s-others-icon\n                  name=\"check-detail\"\n                  :class=\"\n                    $style.toDetail\n                  \"></s-others-icon>\n              </el-link>\n              <u-tooltip\n                v-if=\"!checkPermission\"\n                trigger=\"hover\"\n                placement=\"top\"\n                :hover-delay=\"500\">\n                暂无操作权限\n              </u-tooltip>\n            </div>\n            <el-tree-select\n              class=\"treetag\"\n              v-model=\"model.tags\"\n              style=\"width: 336px\"\n              :data=\"taglist\"\n              multiple\n              :render-after-expand=\"false\"\n              show-checkbox\n              node-key=\"id\"\n              :default-expanded-keys=\"[\n                '全部分类'\n              ]\"\n              @change=\"onSelectTags\"\n              :props=\"{\n                children: 'child',\n                label: 'tag',\n                disabled: 'disable'\n              }\" />\n          </el-form-item>\n          <!-- <div :class=\"$style.dashline\"></div> -->\n          <el-form-item\n            label=\"发布版本\"\n            required\n            layout=\"block\"\n            :rules=\"validateRules.checkVersion\"\n            ref=\"version\"\n            prop=\"version\"\n            for=\"none\">\n            <span\n              :class=\"$style.lastVersionTitle\">\n              (上次导出的版本：{{\n                isEditOldTemp\n                  ? lastVersion || \"暂无\"\n                  : \"暂无\"\n              }}\n              )\n            </span>\n            <s-version\n              v-model:value=\"model.version\"\n              :disabled=\"loading\"\n              min-value=\"1.0.0\"></s-version>\n          </el-form-item>\n          <el-form-item\n            label=\"版本描述\"\n            :rules=\"validateRules.description\"\n            prop=\"description\"\n            for=\"none\">\n            <el-input\n              type=\"textarea\"\n              v-model=\"model.description\"\n              :disabled=\"loading\"\n              maxlength=\"500\"\n              show-word-limit\n              placeholder=\"请输入\"\n              :autosize=\"{\n                minRows: 5\n              }\"></el-input>\n          </el-form-item>\n          <el-form-item\n            label=\"版本配图\"\n            style=\"margin-bottom: 0\"\n            for=\"none\">\n            <s-uploader\n              v-model:file-list=\"model.picture\"\n              multiple\n              list-type=\"picture\"\n              drag\n              action=\"/api/v1/userIcon/upload\"\n              max-size=\"50MB\"\n              accept=\"image/png, image/jpg, image/jpeg, image/gif, image/bmp\"\n              :limit=\"10\"\n              :class=\"$style.upload\">\n              <div :class=\"$style.uploadArea\">\n                <s-others-icon\n                  name=\"upload\"\n                  :class=\"\n                    $style.uploadIcon\n                  \"></s-others-icon>\n                点击/拖拽/粘贴文件到这里\n              </div>\n            </s-uploader>\n          </el-form-item>\n        </el-form>\n      </el-aside>\n      <el-main\n        :class=\"$style.right\"\n        style=\"width: 400px; height: 478px\">\n        <div :class=\"$style.tipwrap\">\n          <div>\n            <span :class=\"$style.tiptext\">\n              请选择能力\n            </span>\n            <span :class=\"$style.required\">\n              *\n            </span>\n            <span\n              :class=\"$style.errmsg\"\n              v-if=\"isEmptyChecked\">\n              <s-others-icon\n                name=\"solid-hint\"\n                :class=\"\n                  $style.iconinfo\n                \"></s-others-icon>\n              请至少选择一项能力\n            </span>\n          </div>\n          <div>\n            <el-checkbox\n              :class=\"$style.autoCheck\"\n              v-model=\"autoCheckdepends\">\n              自动勾选依赖项\n            </el-checkbox>\n            <el-tooltip placement=\"top\">\n              <s-others-icon\n                name=\"hint\"\n                :class=\"\n                  $style.hinticon\n                \"></s-others-icon>\n              <template #content>\n                <div style=\"width: 300px\">\n                  若开发者只想将前端页面导出为模板复用，后端逻辑相关希望让模板使用者自己定义，此时可选择不导出前端页面所依赖的后端逻辑。\n                </div>\n              </template>\n            </el-tooltip>\n          </div>\n        </div>\n        <div\n          v-if=\"loading\"\n          :class=\"$style.loading\">\n          <div v-loading=\"loading\"></div>\n          <div :class=\"$style.text\">\n            数据加载中，请稍后...\n          </div>\n        </div>\n        <div\n          v-else-if=\"empty\"\n          :class=\"$style.loading\">\n          <div :class=\"$style.text\">\n            暂无可导出对象\n          </div>\n        </div>\n        <el-scrollbar\n          v-else\n          :class=\"$style.scrollview\">\n          <el-tree-v2\n            ref=\"treeViewRef\"\n            :data=\"treeData\"\n            :props=\"treeProps\"\n            :height=\"400\"\n            :item-size=\"35\"\n            show-checkbox\n            :default-expanded-keys=\"\n              defaultExpandedKeys\n            \"\n            :default-checked-keys=\"checkedValues\"\n            class=\"s-tree s-tree-export-template\"\n            check-strictly\n            @check-change=\"onCheckChange\"\n            @node-expand=\"setDirectoryExpandIcon\"\n            @node-collapse=\"\n              setDirectoryCollapseIcon\n            \">\n            <template #default=\"{ data: node }\">\n              <u-tooltip\n                trigger=\"hover\"\n                placement=\"left\"\n                :show-after=\"200\"\n                reference=\"prev\"\n                :disabled=\"!node.exportDisabled\">\n                暂不支持导出\n              </u-tooltip>\n              <template\n                v-if=\"\n                  node.concept ===\n                  'ConfigProperty'\n                \">\n                <div :class=\"$style.configWrap\">\n                  <div :class=\"$style.main\">\n                    <s-concept-icon\n                      :name=\"\n                        node.icon || node.concept\n                      \"\n                      :class=\"\n                        $style.conceptIcon\n                      \"></s-concept-icon>\n                    <span>{{ node.name }}</span>\n                  </div>\n                  <div\n                    :class=\"$style.desc\"\n                    v-if=\"node.description\">\n                    {{ node.description }}\n                  </div>\n                </div>\n              </template>\n              <template v-else>\n                <template\n                  v-if=\"\n                    node.concept ===\n                    'ConnectorModuleItem'\n                  \">\n                  <s-upload-icon\n                    v-if=\"\n                      isImage(\n                        getConnectorSetting(node)\n                          .icon\n                      )\n                    \"\n                    displayModeCustom\n                    width=\"14px\"\n                    height=\"14px\"\n                    :can-edit=\"false\"\n                    :prop-icon=\"\n                      getConnectorSetting(node)\n                        .icon\n                    \"\n                    :class=\"$style.uploadIcon\" />\n                  <s-concept-icon\n                    v-else\n                    :name=\"\n                      getConnectorSetting(node)\n                        .icon\n                    \"\n                    :class=\"\n                      $style.conceptIcon\n                    \"></s-concept-icon>\n                </template>\n                <template\n                  v-else-if=\"\n                    node.concept === 'Connection'\n                  \">\n                  <s-upload-icon\n                    v-if=\"\n                      isImage(\n                        node.connector.previewImg,\n                        node.connector\n                          .connectorKind\n                      )\n                    \"\n                    displayModeCustom\n                    width=\"14px\"\n                    height=\"14px\"\n                    :can-edit=\"false\"\n                    :prop-icon=\"\n                      getConnectorWrapperIcon(\n                        node\n                      )\n                    \"\n                    :class=\"$style.uploadIcon\" />\n                  <s-concept-icon\n                    v-else\n                    :name=\"\n                      node.connector.connectorKind\n                    \"\n                    :class=\"\n                      $style.conceptIcon\n                    \"></s-concept-icon>\n                </template>\n                <s-concept-icon\n                  v-else\n                  :name=\"getIcon(node)\"\n                  :class=\"\n                    $style.conceptIcon\n                  \"></s-concept-icon>\n                <div\n                  :class=\"$style.topTitle\"\n                  v-if=\"\n                    node.concept &&\n                    node.concept.endsWith(\n                      'Root'\n                    ) &&\n                    node.concept !==\n                      'DataSourceRoot'\n                  \">\n                  <span\n                    class=\"titellipsis\"\n                    :title=\"\n                      node.aliasName || node.name\n                    \">\n                    {{\n                      node.aliasName || node.name\n                    }}\n                  </span>\n                  <el-link\n                    v-show=\"\n                      node.childNodes.length > 0\n                    \"\n                    @click=\"\n                      onCheckAll($event, node)\n                    \">\n                    {{ getCheckedState(node) }}\n                  </el-link>\n                </div>\n                <div\n                  :class=\"$style.topTitle\"\n                  v-else-if=\"\n                    node.concept === 'DataSource'\n                  \">\n                  <div>\n                    <span\n                      class=\"titellipsis\"\n                      :title=\"\n                        node.aliasName ||\n                        node.name\n                      \">\n                      {{\n                        node.aliasName ||\n                        node.name\n                      }}\n                    </span>\n                    <s-others-icon\n                      name=\"info\"\n                      :class=\"$style.dreficon\">\n                      <u-tooltip\n                        trigger=\"hover\"\n                        placement=\"top\"\n                        :show-after=\"200\">\n                        数据源不会带出数据库链接等信息，不支持数据共享\n                      </u-tooltip>\n                    </s-others-icon>\n                  </div>\n                  <el-link\n                    @click=\"\n                      onCheckAll($event, node)\n                    \"\n                    v-if=\"!node.module\">\n                    {{ getCheckedState(node) }}\n                  </el-link>\n                </div>\n                <span\n                  v-else\n                  class=\"titellipsis\"\n                  :title=\"getTextName(node)\">\n                  {{ getTextName(node) }}\n                </span>\n                <span\n                  v-if=\"\n                    [\n                      'Module',\n                      'ConnectorModuleItem'\n                    ].includes(node.concept)\n                  \">\n                  {{\n                    node.version\n                      ? `（${node.version}）`\n                      : \"\"\n                  }}\n                </span>\n              </template>\n              <s-others-icon\n                v-if=\"\n                  getNodeReferences(node).length\n                \"\n                name=\"depend-reference\"\n                :class=\"$style.dreficon\">\n                <u-tooltip\n                  trigger=\"hover\"\n                  placement=\"top\"\n                  :show-after=\"200\">\n                  <div :class=\"$style.refwrap\">\n                    <span>依赖关系：</span>\n                    <span\n                      v-for=\"(\n                        nref, index\n                      ) in getNodeReferences(\n                        node\n                      )\"\n                      :key=\"nref.name\">\n                      <template\n                        v-if=\"\n                          nref.concept ===\n                          'ConnectorModuleItem'\n                        \">\n                        <s-upload-icon\n                          v-if=\"\n                            isImage(\n                              getConnectorSetting(\n                                nref.node\n                              ).icon\n                            )\n                          \"\n                          displayModeCustom\n                          width=\"14px\"\n                          height=\"14px\"\n                          :can-edit=\"false\"\n                          :prop-icon=\"\n                            getConnectorSetting(\n                              nref.node\n                            ).icon\n                          \"\n                          :class=\"\n                            $style.reficon\n                          \" />\n                        <s-concept-icon\n                          v-else\n                          :name=\"\n                            getConnectorSetting(\n                              nref.node\n                            ).icon\n                          \"\n                          :class=\"\n                            $style.reficon\n                          \"></s-concept-icon>\n                      </template>\n                      <template\n                        v-else-if=\"\n                          nref.concept ===\n                          'Connection'\n                        \">\n                        <s-upload-icon\n                          v-if=\"\n                            isImage(\n                              nref.node.connector\n                                .previewImg,\n                              nref.node.connector\n                                .connectorKind\n                            )\n                          \"\n                          displayModeCustom\n                          width=\"14px\"\n                          height=\"14px\"\n                          :can-edit=\"false\"\n                          :prop-icon=\"\n                            getConnectorWrapperIcon(\n                              nref.node\n                            )\n                          \"\n                          :class=\"\n                            $style.reficon\n                          \" />\n                        <s-concept-icon\n                          v-else\n                          :name=\"\n                            nref.node.connector\n                              .connectorKind\n                          \"\n                          :class=\"\n                            $style.reficon\n                          \"></s-concept-icon>\n                      </template>\n                      <s-concept-icon\n                        v-else\n                        :name=\"getIcon(nref)\"\n                        :class=\"\n                          $style.reficon\n                        \"></s-concept-icon>\n                      <span>{{ nref.name }}</span>\n                      <span\n                        v-if=\"\n                          index !==\n                          getNodeReferences(node)\n                            .length -\n                            1\n                        \">\n                        、\n                      </span>\n                    </span>\n                  </div>\n                </u-tooltip>\n              </s-others-icon>\n            </template>\n          </el-tree-v2>\n        </el-scrollbar>\n      </el-main>\n    </el-container>\n    <template #footer>\n      <div\n        style=\"\n          display: flex;\n          justify-content: space-between;\n        \">\n        <el-space v-if=\"hasPreCheckedNodes\">\n          <span>发布的资产，可前往</span>\n          <el-link @click=\"goAssetCenterSeg\">\n            「资产中心-局部模板」\n          </el-link>\n          <span>查看</span>\n        </el-space>\n        <el-space v-else>&nbsp</el-space>\n        <el-space justify=\"end\">\n          <el-button @click=\"close()\">\n            取 消\n          </el-button>\n          <el-button\n            type=\"primary\"\n            @click=\"onSubmit\"\n            :loading=\"submiting\">\n            发 布\n          </el-button>\n        </el-space>\n      </div>\n    </template>\n  </el-dialog>\n</template>\n\n<script setup>\n  import { inject } from \"vue\"\n\n  const {\n    /**\n     * 表单节点\n     *\n     * @type {Vue.ref<HTMLElement>}\n     */\n    formRef,\n    /**\n     * 树形视图节点\n     *\n     * @type {Vue.ref<HTMLElement>}\n     */\n    treeViewRef,\n    /**\n     * 是否可见\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    visible,\n    /**\n     * 是否加载中\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    loading,\n    /**\n     * 是否为空\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    empty,\n    /**\n     * 是否提交中\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    submiting,\n    /**\n     * 是否编辑旧模板\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    isEditOldTemp,\n    /**\n     * 是否可以编辑图标\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    canEditIcon,\n    /**\n     * 是否自动勾选依赖\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    autoCheckdepends,\n    /**\n     * 是否为空选中\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    isEmptyChecked,\n    /**\n     * 是否有预选节点\n     *\n     * @type {Vue.ref<boolean>}\n     */\n    hasPreCheckedNodes,\n    /**\n     * 是否有权限\n     *\n     * @type {Vue.computed<boolean>}\n     */\n    checkPermission,\n    /**\n     * 表单数据\n     *\n     * @type {Vue.reactive<object>}\n     * @property {string} icon - 图标\n     * @property {array} picture - 图片列表\n     * @property {array} tags - 标签列表\n     * @property {string} symbol - 标识\n     * @property {string} name - 名称\n     * @property {string} version - 版本\n     * @property {string} description - 描述\n     */\n    model,\n    /**\n     * 验证规则\n     *\n     * @type {object}\n     */\n    validateRules,\n    /**\n     * 标签列表\n     *\n     * @type {Vue.ref<array>}\n     */\n    taglist,\n    /**\n     * 历史列表\n     *\n     * @type {Vue.ref<array>}\n     */\n    historyList,\n    /**\n     * 历史编辑列表\n     *\n     * @type {Vue.ref<array>}\n     */\n    historyEditList,\n    /**\n     * 最后一项\n     *\n     * @type {Vue.reactive<object>}\n     * @property {string} name - 名称\n     * @property {string} symbol - 标识\n     * @property {string} value - 值\n     * @property {string} text - 文本\n     */\n    lastItem,\n    /**\n     * 最后版本\n     *\n     * @type {Vue.ref<string>}\n     */\n    lastVersion,\n    /**\n     * 树形数据\n     *\n     * @type {Vue.ref<array>}\n     */\n    treeData,\n    /**\n     * 树形属性\n     *\n     * @type {object}\n     */\n    treeProps,\n    /**\n     * 默认展开的键\n     *\n     * @type {Vue.ref<array>}\n     */\n    defaultExpandedKeys,\n    /**\n     * 选中的值\n     *\n     * @type {Vue.ref<array>}\n     */\n    checkedValues,\n    /**\n     * 关闭对话框\n     *\n     * @function\n     */\n    close,\n    /**\n     * 改变图标\n     *\n     * @function\n     * @param {string} item - 图标值\n     */\n    changeIcon,\n    /**\n     * 编辑为新模板\n     *\n     * @function\n     * @param {Event} e - 事件对象\n     */\n    canEditToNewTemp,\n    /**\n     * 跳转到资产中心\n     *\n     * @function\n     */\n    goAssetCenterSeg,\n    /**\n     * 打开资产配置\n     *\n     * @function\n     */\n    openAssetconfig,\n    /**\n     * 选择模板\n     *\n     * @function\n     * @param {string} symbol - 标识\n     */\n    onSelectSegmentTemp,\n    /**\n     * 选择标签\n     *\n     * @function\n     * @param {array} item - 标签列表\n     */\n    onSelectTags,\n    /**\n     * 切换历史\n     *\n     * @function\n     * @param {object} item - 历史项\n     */\n    toggleHistory,\n    /**\n     * 提交表单\n     *\n     * @function\n     */\n    onSubmit,\n    /**\n     * 检查变更\n     *\n     * @function\n     * @param {object} node - 节点\n     * @param {boolean} checked - 是否选中\n     */\n    onCheckChange,\n    /**\n     * 设置目录展开图标\n     *\n     * @function\n     * @param {object} node - 节点\n     */\n    setDirectoryExpandIcon,\n    /**\n     * 设置目录折叠图标\n     *\n     * @function\n     * @param {object} node - 节点\n     */\n    setDirectoryCollapseIcon,\n    /**\n     * 获取节点引用\n     *\n     * @function\n     * @param {object} node - 节点\n     * @returns {array} 引用列表\n     */\n    getNodeReferences,\n    /**\n     * 获取图标\n     *\n     * @function\n     * @param {object} node - 节点\n     * @returns {string} 图标名称\n     */\n    getIcon,\n    /**\n     * 获取文本名称\n     *\n     * @function\n     * @param {object} node - 节点\n     * @returns {string} 文本名称\n     */\n    getTextName,\n    /**\n     * 获取选中状态\n     *\n     * @function\n     * @param {object} node - 节点\n     * @param {array} allCheckedkeys - 所有选中的键\n     * @returns {string} 选中状态\n     */\n    getCheckedState,\n    /**\n     * 全选\n     *\n     * @function\n     * @param {Event} e - 事件对象\n     * @param {object} node - 节点\n     */\n    onCheckAll,\n    /**\n     * 是否为图片\n     *\n     * @function\n     * @param {string} icon - 图标\n     * @param {string} connectorKind - 连接器类型\n     * @returns {boolean} 是否为图片\n     */\n    isImage,\n    /**\n     * 获取连接器设置\n     *\n     * @function\n     * @param {object} node - 节点\n     * @returns {object} 连接器设置\n     */\n    getConnectorSetting,\n    /**\n     * 获取连接器包装图标\n     *\n     * @function\n     * @param {object} node - 节点\n     * @returns {string} 图标\n     */\n    getConnectorWrapperIcon\n  } = inject(\"$context\")\n\n  if (!validateRules.tags) {\n    validateRules.tags = [\n      {\n        required: true,\n        message: \"请选择模板分类\",\n        trigger: [\"blur\", \"change\"]\n      }\n    ]\n  }\n</script>\n\n<style module>\n  .dashline {\n    border-top: 1px dashed\n      var(--line-color-primary);\n    margin: 8px 0 20px;\n  }\n  .left {\n    border-right: 1px solid\n      var(--depends-tree-view-border-color);\n  }\n  /* .left [class^='el-form-item'] {\n  margin-bottom: 18px;\n} */\n  .left,\n  .right {\n    padding: 24px;\n  }\n  .right {\n    padding-bottom: 0;\n  }\n  .topTitle {\n    display: inline-flex;\n    justify-content: space-between;\n    width: 100%;\n    padding-right: 15px;\n  }\n\n  .conceptIcon {\n    vertical-align: -1px;\n  }\n  .nodeItem[disabled] .conceptIcon,\n  .nodeItem[disabled] .uploadIcon {\n    opacity: 0.6;\n  }\n  .scrollview {\n    height: calc(100% - 50px);\n    /* background: var(--depends-tree-view-background); */\n    border-radius: 4px;\n    background: var(--seg-tree-view-background);\n    /* border: 1px solid var(--seg-tree-view-border-color); */\n  }\n  /* 为了横向滚动hover tree node的时候能够覆盖整条 */\n  .scrollview\n    [class^=\"f-scroll-view_wrap__\"]\n    > div {\n    display: inline-block;\n    min-width: 100%;\n  }\n  .tipwrap {\n    display: flex;\n    justify-content: space-between;\n    margin-bottom: 6px;\n  }\n  .tipwrap .required {\n    color: var(--error-color);\n    margin-left: 4px;\n  }\n  .tipwrap .errmsg {\n    color: var(--error-color);\n    margin-left: 10px;\n  }\n  .tipwrap .iconinfo {\n    margin-right: 2px;\n  }\n  .icontip {\n    font-size: 12px;\n    margin-left: 8px;\n    vertical-align: 1px;\n  }\n  .icontip {\n    font-size: 12px;\n    margin-left: 8px;\n    vertical-align: 1px;\n    /* font-size: 16px; */\n    cursor: pointer;\n    color: var(--group-attr-panel-icon-color);\n    line-height: 0;\n  }\n  .icontip:hover {\n    color: var(\n      --group-attr-panel-icon-color-hover\n    );\n  }\n  .tip {\n    width: 310px;\n    font-size: 14px;\n  }\n  .loading {\n    height: calc(100% - 50px);\n    background: var(\n      --depends-tree-view-background\n    );\n    border-radius: 4px;\n    color: var(\n      --depends-dialog-manage-empty-font-color\n    );\n    text-align: center;\n    padding-top: 42px;\n  }\n  .loading .text {\n    margin-top: 18px;\n  }\n  .reficon {\n    font-size: 16px;\n    margin-right: 2px;\n    vertical-align: -1px;\n  }\n  .tooltip\n    [class^=\"u-tooltip_body__\"]::-webkit-scrollbar {\n    width: 4px;\n  }\n  .tooltip [class^=\"u-tooltip_body__\"] {\n    padding: 6px;\n  }\n  .dreficon {\n    /* composes: icontip; */\n    /* color: var(--depends-export-tooltip-icon-color);\n    font-size: 14px; */\n    cursor: pointer;\n    font-size: 12px;\n    margin-left: 8px;\n    vertical-align: 1px;\n    /* font-size: 16px; */\n    cursor: pointer;\n    color: var(--group-attr-panel-icon-color);\n    line-height: 0;\n  }\n  .dreficon:hover {\n    /* color: #337EFF; */\n    color: var(\n      --group-attr-panel-icon-color-hover\n    );\n  }\n  .submitbtn [class^=\"u-submit-button_error__\"] {\n    left: auto;\n    right: 0;\n    width: 752px;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n  .version label,\n  .tiptext {\n    color: var(\n      --depends-dialog-manage-font-color\n    );\n  }\n  .symbolbox {\n    display: flex;\n    gap: 20px;\n  }\n  .histroy {\n    position: absolute;\n    top: 0;\n    left: 263px;\n    white-space: nowrap;\n  }\n  .tagcategory {\n    position: absolute;\n    top: -30px;\n    right: 0;\n    white-space: nowrap;\n    line-height: 20px;\n  }\n  .tagcategory .toDetail {\n    margin-left: 5px;\n  }\n  .modal [class^=\"u-popup_body__\"] {\n    padding: 0 !important;\n  }\n  .menu {\n    padding: 8px 0;\n  }\n  .menuItem {\n    width: 300px;\n    height: 48px;\n    padding: 10px 12px;\n  }\n  .menuName {\n    font-style: normal;\n    font-weight: 400;\n    font-size: 12px;\n    line-height: 12px;\n    color: #222222;\n    max-width: 180px;\n    overflow: hidden;\n    word-wrap: normal;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n  }\n  .menuSymbol {\n    font-style: normal;\n    font-weight: 400;\n    font-size: 12px;\n    line-height: 12px;\n    color: #999999;\n  }\n  .menuItem:hover {\n    background: #ededed;\n  }\n  .toplevel\n    [class*=\"u-tree-view-node_checkbox_\"] {\n    display: none;\n  }\n  .lowlevel\n    [class*=\"u-tree-view-node_checkbox_\"] {\n    display: inline-block;\n  }\n  .upload {\n   