d\n        layout=\"block\"\n        prop=\"tags\"\n        for=\"none\">\n        <div :class=\"$style.tagcategory\">\n          <el-tooltip\n            v-if=\"!checkPermission\"\n            :content=\"'暂无操作权限'\"\n            trigger=\"hover\"\n            placement=\"top\"\n            :hover-delay=\"500\">\n            <el-link\n              :disabled=\"!checkPermission\"\n              @click=\"openAssetconfig\">\n              分类管理\n              <s-others-icon\n                name=\"check-detail\"\n                :class=\"\n                  $style.toDetail\n                \"></s-others-icon>\n            </el-link>\n          </el-tooltip>\n          <el-link\n            v-if=\"checkPermission\"\n            @click=\"openAssetconfig\">\n            分类管理\n            <s-others-icon\n              name=\"check-detail\"\n              :class=\"\n                $style.toDetail\n              \"></s-others-icon>\n          </el-link>\n        </div>\n        <el-tree-select\n          class=\"treetag\"\n          placeholder=\"请选择模板分类\"\n          v-model=\"model.tags\"\n          style=\"width: 100%\"\n          :data=\"taglist\"\n          multiple\n          :render-after-expand=\"false\"\n          show-checkbox\n          node-key=\"id\"\n          :default-expanded-keys=\"['全部分类']\"\n          @change=\"onSelectTags\"\n          :props=\"{\n            children: 'child',\n            label: 'tag',\n            disabled: 'disable'\n          }\" />\n      </el-form-item>\n      <el-form-item\n        label=\"发布版本\"\n        required\n        :rules=\"validateRules.checkVersion\"\n        layout=\"block\"\n        prop=\"version\">\n        <span :class=\"$style.lastVersionTitle\">\n          (上次导出的版本：{{\n            lastVersion ? lastVersion : \"暂无\"\n          }}\n          )\n        </span>\n        <s-version\n          v-model:value=\"model.version\"\n          min-value=\"1.0.0\"></s-version>\n      </el-form-item>\n\n      <el-form-item\n        label=\"版本描述\"\n        :rules=\"validateRules.description\"\n        prop=\"description\"\n        for=\"none\">\n        <el-input\n          type=\"textarea\"\n          v-model=\"model.description\"\n          placeholder=\"请输入\"></el-input>\n      </el-form-item>\n\n      <el-form-item\n        label=\"模板配图\"\n        layout=\"block\"\n        for=\"none\">\n        <s-uploader\n          v-model:file-list=\"files\"\n          multiple\n          list-type=\"picture\"\n          drag\n          :on-success=\"onUploadSuccess\"\n          action=\"/api/v1/userIcon/upload\"\n          accept=\"image/png, image/jpg, image/jpeg, image/gif, image/bmp\"\n          :limit=\"10\"\n          :class=\"$style.upload\">\n          <div :class=\"$style.uploadArea\">\n            <s-others-icon\n              name=\"upload\"\n              :class=\"\n                $style.uploadIcon\n              \"></s-others-icon>\n            点击/拖拽/粘贴文件到这里\n          </div>\n        </s-uploader>\n      </el-form-item>\n      <el-form-item\n        label=\"下载选择：\"\n        :class=\"$style.formlabelinline\"\n        for=\"none\">\n        <el-radio-group\n          :model-value=\"needPreview\"\n          @change=\"setNeedPreview($event)\">\n          <el-radio :value=\"true\">\n            下载模板+资产中心预览效果\n          </el-radio>\n          <el-radio :value=\"false\">\n            仅下载模板\n          </el-radio>\n        </el-radio-group>\n      </el-form-item>\n    </el-form>\n    <template #footer>\n      <el-space\n        style=\"\n          display: flex;\n          justify-content: space-between;\n        \"\n        justify=\"end\">\n        <el-space>\n          <el-button\n            @click=\"showMockModal()\"\n            v-if=\"needPreview\">\n            编辑预览数据\n          </el-button>\n        </el-space>\n\n        <el-space justify=\"end\">\n          <el-button @click=\"close()\">\n            取 消\n          </el-button>\n          <el-button\n            type=\"primary\"\n    