   * 打开资产配置方法\n     *\n     * @function\n     */\n    openAssetconfig,\n    /**\n     * 标签列表数据\n     *\n     * @type {Array<object>}\n     * @property {string} id - 标签ID\n     * @property {string} tag - 标签名称\n     * @property {Array} child - 子标签列表\n     * @property {boolean} disable - 是否禁用\n     */\n    taglist,\n    /**\n     * 选择标签回调方法\n     *\n     * @function\n     * @param {Array} tags - 选中的标签值\n     */\n    onSelectTags,\n    /**\n     * 上次导出的版本号\n     *\n     * @type {string}\n     */\n    lastVersion,\n    /**\n     * 上传文件列表\n     *\n     * @type {Array<object>}\n     */\n    files,\n    /**\n     * 上传成功回调方法\n     *\n     * @function\n     * @param {object} response - 上传响应数据\n     */\n    onUploadSuccess,\n    /**\n     * 是否需要预览\n     *\n     * @type {boolean}\n     */\n    needPreview,\n    /**\n     * 显示Mock数据模态框方法\n     *\n     * @function\n     */\n    showMockModal,\n    /**\n     * 提交表单方法\n     *\n     * @function\n     */\n    onSubmit,\n    /**\n     * 是否正在提交中\n     *\n     * @type {boolean}\n     */\n    submiting,\n    /**\n     * 当前角色回调方法\n     *\n     * @function\n     * @param {string} role - 当前角色\n     */\n    currentRole,\n    /**\n     * 获取Mock数据列表方法\n     *\n     * @function\n     * @param {Array} list - Mock数据列表\n     */\n    getMockList,\n    /**\n     * 设置表单验证状态方法\n     *\n     * @function\n     * @param {boolean} valid - 验证状态\n     */\n    setValid,\n    /**\n     * 设置是否需要预览方法\n     *\n     * @function\n     * @param {boolean} value - 是否需要预览\n     */\n    setNeedPreview\n  } = inject(\"$context\")\n\n  if (!validateRules.value.tags) {\n    validateRules.value.tags = [\n      {\n        required: true,\n        message: \"请选择模板分类\",\n        trigger: [\"blur\", \"change\"]\n      }\n    ]\n  }\n</script>\n\n<style module>\n  .form {\n    padding: 8px;\n  }\n\n  .tagcategory {\n    position: absolute;\n    top: -28px;\n    right: 0px;\n    white-space: nowrap;\n  }\n\n  .iconbox {\n    display: flex;\n    justify-content: space-between;\n    /*    margin-bottom:20px;*/\n  }\n  .upload {\n    width: 100%;\n  }\n  .upload [class^=\"el-upload-dragger\"] {\n    background-color: var(\n      --uploader-draggable-background\n    );\n    padding: 19px;\n  }\n  .uploadArea {\n    color: var(--uploader-draggable-color);\n    pointer-events: none;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-direction: column;\n  }\n\n  .uploadIcon {\n    font-size: 24px;\n  }\n  .lastVersionTitle {\n    position: absolute;\n    top: -35px;\n    left: 70px;\n    color: #666;\n  }\n  .formlabelinline {\n    display: flex !important;\n    margin-bottom: 0;\n  }\n  .formlabelinline\n    > [class^=\"el-form-item__label\"] {\n    display: flex !important;\n    align-items: center;\n    margin-bottom: 0 !important;\n  }\n</style>\n"],"sourceRoot":""}]);
